.checkout {
  background-color: white;
}

.checkout__container > h1 {
  text-align: center;
  padding: 10px;
  font-weight: 400;
  background-color: rgb(234, 237, 237);
  border-bottom: 1px solid lightgray;
}

.checkout__container > h1 a {
  text-decoration: none;
}

.checkout__section {
  display: flex;
  padding: 20px;
  margin: 0 20px;
  border-bottom: 1px solid lightgray;
}

.checkout__title {
  flex: 0.2;
  font-size: 20px;
  font-weight: 700;
}

.checkout__info,
.checkout__address,
.checkout__details {
  flex: 0.8;
  font-size: 18px;
}

.checkout__items {
  flex: 0.25;
}

.checkout__info > p {
  font-size: 18px;
  line-height: 20px;
}

.checkout__details > form {
  max-width: 400px;
}

.checkout__details > h3 {
  padding-bottom: 20px;
}

.checkout__details > div > button {
  background: #f0c14b;
  border-radius: 2px;
  width: 100%;
  height: 30px;
  border: 1px solid;
  font-weight: bolder;
  margin-top: 10px;
  border-color: #a88734 #9c7e31 #846a29;
  color: #111;
}
